/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
      outline: none;
  }

  &.dragging {
      cursor: pointer;
      cursor: hand;
  }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}


.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;

  &:before,
  &:after {
      content: "";
      display: table;
  }

  &:after {
      clear: both;
  }

  .slick-loading & {
      visibility: hidden;
  }
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  [dir="rtl"] & {
      float: right;
  }
  img {
      display: block;
  }
  &.slick-loading img {
      display: none;
  }

  display: none;

  &.dragging img {
      pointer-events: none;
  }

  .slick-initialized & {
      display: block;
  }

  .slick-loading & {
      visibility: hidden;
  }

  .slick-vertical & {
      display: block;
      height: auto;
      border: 1px solid transparent;
  }
}
.slick-arrow.slick-hidden {
  display: none;
}

.images {
height: 100%;
width: 100%;
}

//*CARD OVERLAY

.homepage-card-wrapper,
.recommended-card-wrapper,
.showpage-similar-card-wrapper {
  position: relative;
  font-size: 20px;
}

.homepage-card-overlay,
.recommended-slider-card-overlay,
.showpage-card-overlay {
  position: absolute;
  width: 7.7%;
  height: 100%;
  top: 0.1px;
  //bottom: 5px;
  float: left;
  padding: 15px;

  justify-content: center;
  align-items: center;
  display: flex;
  text-align: center;

  background-color: #FFDA1A;
  color:  #175fac;
  font-family: 'Quicksand', sans-serif;
  font-size: 20px;
  
  opacity: 0;
  transition: .5s ease;
  
}

.homepage-card-name:hover .homepage-card-overlay,
.recommended-card-wrapper:hover .recommended-slider-card-overlay,
.showpage-similar-card-wrapper:hover .showpage-card-overlay {
  opacity: 1;
}